<template>
	<div class="banner">
        <ul class="clearfix">
            <li><img src="http://cdn.jirengu.com/book.jirengu.com/img/26.jpg" alt=""/>
            	<div class="text-box">
                	<h2></h2>
                    <p> / </p>
                </div>
            </li>
            <li><img src="http://cdn.jirengu.com/book.jirengu.com/img/25.jpg" alt=""/>
            	<div class="text-box">
                	<h2>1800</h2>
                    <p> / </p>
                </div>
            </li>
            <li><img src="http://cdn.jirengu.com/book.jirengu.com/img/24.jpg" alt=""/>
            	<div class="text-box">
                	<h2></h2>
                    <p> / </p>
                </div>
            </li>
            <li><img src="http://cdn.jirengu.com/book.jirengu.com/img/23.jpg" alt=""/>
            	<div class="text-box">
                	<h2></h2>
                    <p> / </p>
                </div>
            </li>
        </ul>
        <ol>
            <li class="active"></li>
            <li></li>
            <li></li>
            <li></li>
        </ol>
    </div>
</template>
<script>
	export default {
		mounted(){
			this.fnSlider();
		},
		methods:{
			fnSlider(){
				$(function (){
					var mySwipe=new Swipe($('.banner')[0],{
						auto:2000,
						continuous:true,
						stopPropation:true,
						callback:function (index,element){
							$('.banner ol li').removeClass('active');
							$('.banner ol li').eq(index).addClass('active');
						}
					});
				});
			}
		}
	}
</script>


